.om-button {
  padding: 0.1rem 0.2rem;
  border-radius: 0.05rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.om-button.is-circle {
  border-radius: 0.3rem;
}

.om-button--large {
  font-size: 0.28rem;
  padding: 0.2rem 0.24rem;
}

.om-button--large span,
.om-button--large .iconfont {
  font-size: 0.28rem;
}

.om-button--small {
  padding: 0.08rem 0.14rem;
  font-size: 0.2rem;
}

.om-button--small span,
.om-button--small .iconfont {
  font-size: 0.2rem;
}

.om-button--primary {
  color: #fff;
  background-color: #409eff;
  text-align: center;
  outline: none;
  border: 0.01rem solid #409eff;
}

.om-button--primary.outline {
  background-color: #fff;
  color: #409eff;
}

.om-button--success {
  color: #fff;
  background-color: #67c23a;
  text-align: center;
  outline: none;
  border: 0.01rem solid #67c23a;
}

.om-button--success.outline {
  background-color: #fff;
  color: #67c23a;
}

.om-button--warning {
  color: #fff;
  background-color: #e6a23c;
  text-align: center;
  outline: none;
  border: 0.01rem solid #e6a23c;
}

.om-button--warning.outline {
  background-color: #fff;
  color: #e6a23c;
}

.om-button--danger {
  color: #fff;
